import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import Home from "./components/Home";
import About from './components/About';



import './App.css';



function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route exact path="/about">
            <About />
          </Route>
          <Route exact path="/">
            <Home />
          </Route>
        </Switch>
    </div>
    </Router>
    
  );
}

export default App;
